'use client';
import React from 'react';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Avatar from '@mui/material/Avatar';
import Chip from '@mui/material/Chip';
import Divider from '@mui/material/Divider';
import FormatQuoteIcon from '@mui/icons-material/FormatQuote';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { aboutData } from '@/utils/aboutData';

const About = () => {
  return (
    <Box sx={{ 
      py: { xs: 6, md: 10 },
      backgroundColor: 'background.default',
      minHeight: '100vh'
    }}>
      <Container maxWidth="lg">
        
        {/* Hero Section */}
        <Box sx={{ textAlign: 'center', mb: { xs: 6, md: 10 } }}>
          <Chip 
            label="AI-Powered Analysis" 
            color="primary" 
            sx={{ mb: 3, fontWeight: 'bold', fontSize: '0.9rem' }}
          />
          <Typography 
            variant="h2" 
            component="h1" 
            gutterBottom 
            sx={{ 
              fontWeight: 'bold',
              fontSize: { xs: '2rem', md: '3rem' },
              mb: 2
            }}
          >
            {aboutData.hero.title}
          </Typography>
          <Typography 
            variant="h5" 
            sx={{ 
              color: 'primary.main',
              fontWeight: 'medium',
              mb: 2,
              fontSize: { xs: '1.25rem', md: '1.5rem' }
            }}
          >
            {aboutData.hero.subtitle}
          </Typography>
          <Typography 
            variant="body1" 
            color="text.secondary" 
            sx={{ 
              maxWidth: 800, 
              mx: 'auto',
              fontSize: { xs: '1rem', md: '1.1rem' }
            }}
          >
            {aboutData.hero.description}
          </Typography>
        </Box>

        {/* 7 Analysis Results */}
        <Box sx={{ mb: { xs: 8, md: 12 } }}>
          <Typography 
            variant="h3" 
            component="h2" 
            gutterBottom 
            sx={{ 
              textAlign: 'center', 
              mb: 6, 
              fontWeight: 'bold',
              fontSize: { xs: '1.75rem', md: '2.5rem' }
            }}
          >
            Your Complete Analysis Includes
          </Typography>
          
          <Grid container spacing={4}>
            {aboutData.analysisResults.map((result, index) => (
              <Grid item xs={12} md={6} lg={4} key={index}>
                <Card 
                  sx={{ 
                    height: '100%',
                    transition: 'transform 0.3s ease-in-out, box-shadow 0.3s',
                    '&:hover': {
                      transform: 'translateY(-8px)',
                      boxShadow: 6,
                    },
                    border: '1px solid',
                    borderColor: 'divider',
                    position: 'relative',
                    overflow: 'visible'
                  }}
                >
                  {/* Number Badge */}
                  <Box
                    sx={{
                      position: 'absolute',
                      top: -20,
                      left: 20,
                      width: 50,
                      height: 50,
                      borderRadius: '50%',
                      backgroundColor: 'primary.main',
                      color: 'white',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      fontSize: '1.2rem',
                      fontWeight: 'bold',
                      boxShadow: 3
                    }}
                  >
                    {result.number}
                  </Box>
                  
                  <CardContent sx={{ p: 4, pt: 5 }}>
                    {/* Icon */}
                    <Box sx={{ 
                      display: 'flex', 
                      justifyContent: 'center', 
                      mb: 3,
                      color: 'primary.main'
                    }}>
                      {result.icon}
                    </Box>
                    
                    {/* Title */}
                    <Typography 
                      variant="h5" 
                      component="h3" 
                      gutterBottom 
                      sx={{ 
                        fontWeight: 'bold',
                        textAlign: 'center',
                        fontSize: { xs: '1.25rem', md: '1.5rem' }
                      }}
                    >
                      {result.title}
                    </Typography>
                    
                    {/* Description */}
                    <Typography 
                      variant="body1" 
                      color="text.secondary" 
                      sx={{ 
                        mb: 3,
                        textAlign: 'center',
                        fontSize: '0.95rem'
                      }}
                    >
                      {result.description}
                    </Typography>
                    
                    {/* Details */}
                    <Paper 
                      elevation={0}
                      sx={{ 
                        p: 2,
                        backgroundColor: 'action.hover',
                        borderRadius: 1
                      }}
                    >
                      {result.details.types && (
                        <Box sx={{ mb: 2 }}>
                          <Typography variant="subtitle2" sx={{ fontWeight: 'bold', mb: 1 }}>
                            Types Detected:
                          </Typography>
                          <Stack direction="row" spacing={1} flexWrap="wrap" useFlexGap>
                            {result.details.types.map((type, idx) => (
                              <Chip 
                                key={idx}
                                label={type} 
                                size="small"
                                variant="outlined"
                              />
                            ))}
                          </Stack>
                        </Box>
                      )}
                      
                      {result.details.features && (
                        <Box sx={{ mb: 2 }}>
                          <Typography variant="subtitle2" sx={{ fontWeight: 'bold', mb: 1 }}>
                            Features Analyzed:
                          </Typography>
                          <Typography variant="body2" color="text.secondary">
                            {result.details.features.join(', ')}
                          </Typography>
                        </Box>
                      )}
                      
                      {result.details.accuracy && (
                        <Typography variant="caption" sx={{ color: 'success.main', fontWeight: 'medium' }}>
                          ✓ {result.details.accuracy}
                        </Typography>
                      )}
                    </Paper>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Box>

        {/* How It Works */}
        <Paper 
          elevation={0}
          sx={{ 
            p: { xs: 4, md: 6 },
            mb: { xs: 8, md: 10 },
            backgroundColor: 'background.paper',
            borderRadius: 3,
            border: '1px solid',
            borderColor: 'divider'
          }}
        >
          <Typography 
            variant="h4" 
            component="h2" 
            gutterBottom 
            sx={{ 
              textAlign: 'center', 
              mb: 5, 
              fontWeight: 'bold' 
            }}
          >
            {aboutData.howItWorks.title}
          </Typography>
          
          <Grid container spacing={4}>
            {aboutData.howItWorks.steps.map((step, index) => (
              <Grid item xs={12} sm={6} md={3} key={index}>
                <Box sx={{ textAlign: 'center' }}>
                  <Avatar
                    sx={{
                      width: 60,
                      height: 60,
                      bgcolor: 'primary.main',
                      color: 'white',
                      fontSize: '1.5rem',
                      fontWeight: 'bold',
                      mx: 'auto',
                      mb: 2
                    }}
                  >
                    {step.step}
                  </Avatar>
                  <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold' }}>
                    {step.title}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    {step.description}
                  </Typography>
                </Box>
              </Grid>
            ))}
          </Grid>
        </Paper>

        {/* Benefits */}
        <Box sx={{ mb: { xs: 8, md: 10 } }}>
          <Typography 
            variant="h4" 
            component="h2" 
            gutterBottom 
            sx={{ 
              textAlign: 'center', 
              mb: 5, 
              fontWeight: 'bold' 
            }}
          >
            Why Choose Our Analysis
          </Typography>
          
          <Grid container spacing={3}>
            {aboutData.benefits.map((benefit, index) => (
              <Grid item xs={12} sm={6} key={index}>
                <Paper 
                  elevation={0}
                  sx={{ 
                    p: 3,
                    height: '100%',
                    display: 'flex',
                    alignItems: 'flex-start',
                    backgroundColor: 'background.paper',
                    border: '1px solid',
                    borderColor: 'divider',
                    borderRadius: 2
                  }}
                >
                  <Box sx={{ 
                    color: 'primary.main',
                    mr: 2,
                    mt: 0.5
                  }}>
                    {benefit.icon}
                  </Box>
                  <Box>
                    <Typography variant="h6" gutterBottom sx={{ fontWeight: 'bold' }}>
                      {benefit.title}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {benefit.description}
                    </Typography>
                  </Box>
                </Paper>
              </Grid>
            ))}
          </Grid>
        </Box>

        {/* Technology */}
        <Paper 
          elevation={0}
          sx={{ 
            p: { xs: 4, md: 6 },
            mb: { xs: 8, md: 10 },
            backgroundColor: 'primary.light',
            backgroundImage: 'linear-gradient(rgba(25, 118, 210, 0.08), rgba(25, 118, 210, 0.12))',
            borderRadius: 3
          }}
        >
          <Typography 
            variant="h4" 
            component="h2" 
            gutterBottom 
            sx={{ 
              textAlign: 'center', 
              mb: 4, 
              fontWeight: 'bold',
              color: 'primary.dark'
            }}
          >
            {aboutData.technology.title}
          </Typography>
          
          <Grid container spacing={3}>
            {aboutData.technology.points.map((point, index) => (
              <Grid item xs={12} sm={6} key={index}>
                <Box sx={{ display: 'flex', alignItems: 'flex-start' }}>
                  <CheckCircleIcon 
                    sx={{ 
                      color: 'primary.main', 
                      mr: 2,
                      mt: 0.5
                    }} 
                  />
                  <Typography variant="body1">
                    {point}
                  </Typography>
                </Box>
              </Grid>
            ))}
          </Grid>
        </Paper>

        {/* Testimonials */}
        <Box sx={{ mb: { xs: 8, md: 10 } }}>
          <Typography 
            variant="h4" 
            component="h2" 
            gutterBottom 
            sx={{ 
              textAlign: 'center', 
              mb: 5, 
              fontWeight: 'bold' 
            }}
          >
            User Success Stories
          </Typography>
          
          <Grid container spacing={4}>
            {aboutData.testimonials.map((testimonial, index) => (
              <Grid item xs={12} md={4} key={index}>
                <Card sx={{ height: '100%' }}>
                  <CardContent sx={{ p: 4 }}>
                    <FormatQuoteIcon 
                      sx={{ 
                        fontSize: 40,
                        color: 'primary.light',
                        mb: 2
                      }} 
                    />
                    <Typography 
                      variant="body1" 
                      sx={{ 
                        fontStyle: 'italic',
                        mb: 3,
                        color: 'text.primary'
                      }}
                    >
                      &ldquo;{testimonial.quote}&rdquo;
                    </Typography>
                    <Divider sx={{ my: 2 }} />
                    <Box>
                      <Typography variant="subtitle1" sx={{ fontWeight: 'bold' }}>
                        {testimonial.author}
                      </Typography>
                      <Typography variant="caption" color="success.main" sx={{ fontWeight: 'medium' }}>
                        {testimonial.improvement}
                      </Typography>
                    </Box>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Box>

        {/* CTA Section */}
        <Paper 
          elevation={0}
          sx={{ 
            p: { xs: 4, md: 6 },
            textAlign: 'center',
            backgroundColor: 'primary.main',
            color: 'white',
            borderRadius: 3,
            background: 'linear-gradient(135deg, #1976d2 0%, #2196f3 100%)'
          }}
        >
          <Typography 
            variant="h3" 
            component="h2" 
            gutterBottom 
            sx={{ 
              fontWeight: 'bold',
              mb: 3,
              fontSize: { xs: '1.75rem', md: '2.5rem' }
            }}
          >
            Ready for Your Analysis?
          </Typography>
          <Typography 
            variant="h6" 
            sx={{ 
              mb: 4, 
              opacity: 0.9,
              fontSize: { xs: '1.1rem', md: '1.25rem' }
            }}
          >
            Discover your 7 personalized insights in minutes
          </Typography>
          
          <Stack 
            direction={{ xs: 'column', sm: 'row' }} 
            spacing={2} 
            justifyContent="center"
            sx={{ mt: 4 }}
          >
            <Button
              variant="contained"
              size="large"
              sx={{
                px: 5,
                py: 1.5,
                backgroundColor: 'white',
                color: 'primary.main',
                fontWeight: 'bold',
                fontSize: '1rem',
                '&:hover': {
                  backgroundColor: 'grey.100',
                  transform: 'translateY(-2px)'
                },
                transition: 'all 0.3s'
              }}
            >
              Start Free Analysis
            </Button>
            <Button
              variant="outlined"
              size="large"
              sx={{
                px: 5,
                py: 1.5,
                borderColor: 'white',
                color: 'white',
                fontWeight: 'bold',
                fontSize: '1rem',
                '&:hover': {
                  backgroundColor: 'rgba(255, 255, 255, 0.1)',
                  borderColor: 'white'
                }
              }}
            >
              View Sample Report
            </Button>
          </Stack>
          
          <Typography 
            variant="caption" 
            sx={{ 
              display: 'block', 
              mt: 4,
              opacity: 0.8
            }}
          >
            No credit card required • 100% private • Results in 30 seconds
          </Typography>
        </Paper>
      </Container>
    </Box>
  );
};

export default About;
